import {getBarList, setBar, upLogo} from '../../api/api.js';

export default {
	data() {
		return {
			location: '',
			barInfo: '',
			itemStr: ''
		}
	},
	methods: {
		getBarListData() {
			getBarList(this.location).then(data => {
				this.barInfo = data;
			});
		},
		navigatorParam(item) {
			this.itemStr = JSON.stringify(item);
			uni.navigateTo({
				url: 'entertainDetail?itemStr=' + this.itemStr

			});
		}
	},
	onLoad() {
		this.getBarListData();
	}
}
